'use client';

import { useState } from 'react';
import { useAuth } from '@/lib/context/AuthContext';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { UserRole } from '@/hooks/use-role';

const ROLE_OPTIONS = [
  { value: 'buyer' as UserRole, label: 'üé´ Buyer', description: 'Buy tickets for events' },
  { value: 'organizer' as UserRole, label: 'üé™ Organizer', description: 'Create and manage events' },
  { value: 'promoter' as UserRole, label: 'üì¢ Promoter', description: 'Promote events and earn commissions' },
  { value: 'venue-owner' as UserRole, label: 'üèüÔ∏è Venue Owner', description: 'List and manage venues' },
  { value: 'artist' as UserRole, label: 'üé§ Artist', description: 'Perform at events' },
  { value: 'reseller' as UserRole, label: 'üîÑ Reseller', description: 'Resell tickets on marketplace' },
];

export function LoginForm() {
  const [email, setEmail] = useState('');
  const [name, setName] = useState('');
  const [selectedRole, setSelectedRole] = useState<UserRole>('buyer');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const { login } = useAuth();

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setLoading(true);

    try {
      await login(email, name || undefined);
      
      // Store selected role for demo purposes
      if (typeof window !== 'undefined') {
        localStorage.setItem('demo-role', selectedRole);
      }
      
      // Redirect based on role
      const roleRoutes: Record<UserRole, string> = {
        'guest': '/',
        'buyer': '/buyer',
        'organizer': '/organizer',
        'promoter': '/promoter',
        'venue-owner': '/venue-owner',
        'artist': '/artist',
        'reseller': '/reseller',
        'admin': '/admin',
        'inspector': '/inspector',
      };

      const redirectPath = roleRoutes[selectedRole] || '/buyer';
      window.location.href = redirectPath;
      
    } catch (err: any) {
      setError(err.message || 'Login failed');
    } finally {
      setLoading(false);
    }
  };

  const selectedRoleInfo = ROLE_OPTIONS.find(r => r.value === selectedRole);

  return (
    <Card className="w-full max-w-md glass-card border-white/20 bg-background/95 backdrop-blur-xl">
      <CardHeader className="text-center">
        <CardTitle className="text-2xl font-bold text-gradient-neon">Welcome to TicketChain</CardTitle>
        <CardDescription className="text-gray-400">
          Choose your role and login to access your Web3 wallet
        </CardDescription>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Role Selection */}
          <div className="space-y-3">
            <Label htmlFor="role" className="text-white font-semibold">I want to join as:</Label>
            <Select value={selectedRole} onValueChange={(value: UserRole) => setSelectedRole(value)}>
              <SelectTrigger className="bg-white/5 border-white/20 text-white">
                <SelectValue />
              </SelectTrigger>
              <SelectContent className="bg-gray-900 border-white/20">
                {ROLE_OPTIONS.map((role) => (
                  <SelectItem key={role.value} value={role.value} className="text-white hover:bg-white/10">
                    <div className="flex flex-col items-start">
                      <span className="font-medium">{role.label}</span>
                      <span className="text-xs text-gray-400">{role.description}</span>
                    </div>
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
            {selectedRoleInfo && (
              <p className="text-xs text-gray-400 bg-white/5 p-2 rounded border border-white/10">
                {selectedRoleInfo.description}
              </p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="email" className="text-white">Email</Label>
            <Input
              id="email"
              type="email"
              placeholder="your@email.com"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="bg-white/5 border-white/20 text-white placeholder:text-gray-400"
              required
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="name" className="text-white">Name (optional)</Label>
            <Input
              id="name"
              type="text"
              placeholder="Your Name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              className="bg-white/5 border-white/20 text-white placeholder:text-gray-400"
            />
          </div>

          {error && (
            <div className="text-sm text-red-400 bg-red-500/10 border border-red-500/20 p-3 rounded-lg">
              {error}
            </div>
          )}

          <Button 
            type="submit" 
            className="w-full gradient-purple-cyan hover:opacity-90 border-0 text-white h-12 font-semibold neon-glow" 
            disabled={loading}
          >
            {loading ? 'Creating Account...' : `Join as ${selectedRoleInfo?.label || 'User'}`}
          </Button>

          <p className="text-xs text-gray-400 text-center">
            üîê A secure crypto wallet will be created for you automatically
          </p>
        </form>
      </CardContent>
    </Card>
  );
}
